*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  width: 100%;
  overflow: hidden;
  background-color: $white;
  font-family: 'Norms', sans-serif;
  color: $mako;
}

h1,
h2,
h3,
h4 {
  margin: 0;
}

img {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
}

ul,
li {
  list-style: none;
}

a {
  text-decoration: none;
}

svg {
  width: 100%;
  height: 100%;
}

.container {
  width: 100%;
  max-width: 1260px;
  padding: 0 15px;
  margin: 0 auto;
}

.first-screen {
  width: 100%;
  background-image: url('../img/header-bg.jpg');
  background-size: cover;
  background-repeat: no-repeat;

  & h1 {
    display: none;
  }
}

.title {
  font-weight: 700;
  font-size: 4.8rem;
  color: $black;
}

.title-price {
  width: 100%;
  background-image: url('../img/subtract.svg');
  background-repeat: no-repeat;
  font-weight: 800;
  color: $black;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 20px 25px;
}
